* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

:root {
	--light-green: #64bf3a;
	--dark-green: #1a9349;
}

.marketplace-cont {
	max-width: 95%;
	margin: 0 auto;
	/* border: 1px solid red; */
	padding-bottom: 50px;
}

::-webkit-scrollbar {
	width: 0px;
}

.marketplace-cont .marketplace-head {
	margin-top: 10px;
	max-width: 100%;
	/* border: 1px solid red; */
	display: grid;
	place-items: center;
	grid-template-areas:
		"a c"
		"b c";
	grid-template-columns: 1fr 200px;
	grid-template-rows: 45px 1fr;
	position: sticky;
	top: 0;
	background-color: #fff;
	z-index: 2;
}

.marketplace-cont .marketplace-head h2 {
	/* border: 1px solid blue; */
	padding: 0;
	grid-area: a;
	color: grey;
	text-transform: uppercase;
	font-size: 3rem;
	text-align: center;
}

.marketplace-cont .marketplace-head .marketplace-buttons-cont {
	/* border: 1px solid grey; */
	grid-area: b;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	padding-inline: 10%;
	margin: 10px 0;
}

.marketplace-cont .marketplace-head .marketplace-buttons-cont button {
	margin: 5px;
	border-radius: 15px;
	border: none;
	outline: none;
	background: linear-gradient(-45deg,
			var(--dark-green),
			var(--light-green),
			var(--dark-green));
	width: 120px;
	height: 40px;
	font-size: 1.5rem;
	font-weight: bolder;
	text-transform: uppercase;
	color: white;
	cursor: pointer;
	box-shadow: rgba(0, 0, 0, 0.35) 0px 1px 7px;
}

.marketplace-cont .marketplace-head .marketplace-buttons-cont button:hover {
	background: linear-gradient(-45deg,
			var(--light-green),
			var(--dark-green),
			var(--light-green));
}

.marketplace-cont .marketplace-head .marketplace-buttons-cont button:active {
	box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
		rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.marketplace-cont .marketplace-head .marketplace-head-right {
	grid-area: c;
	background-color: rgb(15, 117, 10);
	border-radius: 20px;
	display: grid;
	padding: 4px 0;
	grid-template-areas:
		"x y"
		"x z";
	/* border: 1px solid red; */
}

.marketplace-cont .marketplace-head .marketplace-head-right p {
	text-align: center;
	position: relative;
	left: -15px;
	top: 3px;
	color: white;
	font-size: 18px;
	/* border: 1px solid red; */
	margin: 0;
	z-index: 0;
}

.marketplace-cont .marketplace-head .marketplace-head-right p:nth-child(3) {
	font-weight: bolder;
	/* border: 1px solid red; */
	padding-bottom: 4px;
}

.marketplace-cont .marketplace-head .marketplace-head-right .hijklmno {
	grid-area: x;
	border: 2px solid white;
	background: url(../../images/logoo.png);
	background-repeat: no-repeat;
	background-size: 100%;
	background-color: var(--light-green);
	border-radius: 10px;
	position: relative;
	left: -20px;
	top: 2px;
	background-position: center;
	width: 50px;
	height: 50px;
}

.marketplace-cont .marketplace-main-cont {
	overflow: hidden;

	margin-top: 10px;
	/* border: 1px solid red; */
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: 10px 50px;
	gap: 40px 50px;
}

.marketplace-cont .marketplace-main-cont .user-nft {
	width: 200px;
	height: 300px;
	display: grid;
	padding: 0 10px;
	justify-content: center;
	cursor: pointer;
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-img {
	box-shadow: 0px 120px 2px 24px rgb(173, 205, 237);
	width: 170px;
	height: 170px;
	/* border: 1px solid green; */
	margin: 0 auto;
	display: grid;
	place-items: center;
	border-radius: 5px;
	overflow: hidden;
	/* grid-area: m; */
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-img img {
	width: 100%;
	height: 100%;
	overflow: hidden;
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-details {
	padding: 0 15px;
	display: grid;
	gap: 4px;
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-details p:first-child {
	margin-top: 10px;
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-details p {
	/* grid-area: n; */
	font-weight: bold;
	font-size: 1.1rem;
	text-align: left !important;
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-bottom {
	/* border: 1px solid; */
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	height: 35px;
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-bottom h2 {
	color: rgb(14, 66, 1);
	font-weight: 600;
	font-size: 2.8rem;
}

.marketplace-cont .marketplace-main-cont .user-nft .nft-bottom button {
	font-size: 1.8rem;
	width: 75px;
	height: 30px;
	background-color: rgb(14, 66, 1);
	border: none;
	outline: none;
	border-radius: 20px;
	color: white !important;
	font-weight: 900;
	letter-spacing: 0.8px;
}


.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content {
	width: min-content;
	margin: 30% auto;
	height: max-content;
	border-radius: 14px;
	border: none;
}

.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .modal-body {
	background: linear-gradient(135deg, #fff, rgb(227, 227, 227), #5d5d5d);
	border-radius: 14px;
}


.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft {
	height: max-content;
}

.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-img {
	box-shadow: none;
	border-radius: 24px;
}

.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-details {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

}

.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-details p {
	width: fit-content;
	margin: 5px 0;
}

.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-details button {
	padding: 3px;
	margin: 10px 0px;
	font-weight: bolder;
	font-size: small;
	background-color: #ffffff;
	box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
	border: 1.5px solid grey;
}


.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-bottom button {
	width: 150px;
	background-color: #25c11f;
}

.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .modal-header .btn-close {
	background-color: #09ff00;
	font-size: 1.5rem;
}


.modal-backdrop {
	width: 100%;
	height: 100%;
}

.marketplace-cont .alert {
	text-align: center;
	font-size: x-large;
	width: 80%;
	margin: 0 auto;
	z-index: 0;
}

@media (max-width: 640px) {
	.marketplace-cont .marketplace-head {
		grid-template-columns: 1fr;
		grid-template-areas:
			"a"
			"c"
			"b";
		grid-template-rows: 45px 1fr 1fr;
	}
	.marketplace-cont .marketplace-head .marketplace-buttons-cont {
		padding-inline: 0px;
	}

	.marketplace-cont .marketplace-main-cont .user-nft {
		width: 450px;
		height: 550px;
		/* border: 1px solid; */
	}

	.marketplace-cont .marketplace-main-cont .user-nft .nft-img {
		box-shadow: 0px 170px 2px 54px rgb(173, 205, 237);
		width: 380px;
		height: 370px;
	}

	.marketplace-cont .marketplace-main-cont .user-nft .nft-details p {
		font-size: 2.0rem;
	}

	.marketplace-cont .marketplace-main-cont {
		gap: 90px 50px;
		padding-bottom: 70px;
	}

	.marketplace-cont .marketplace-head h2 {
		height: 40px;
	}

	.marketplace-cont .marketplace-main-cont .user-nft .nft-bottom h2 {
		color: rgb(14, 66, 1);
		font-weight: 600;
		font-size: 4.8rem;
	}

	.marketplace-cont .marketplace-main-cont .user-nft .nft-bottom button {
		font-size: 3.2rem;
		width: 115px;
		height: 50px;
	}

	.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content {
		margin: 20%;
	}
	.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-bottom {
		margin:10px 0;
	}
}

@media (max-width: 380px) {
	.marketplace-cont .marketplace-head {
		grid-template-columns: 1fr;
		grid-template-areas:
			"a"
			"c"
			"b";
		grid-template-rows: 40px 85px 1fr;
	}

	.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content {
		margin: 60%;
	}
	.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-bottom {
		margin:20px 0;
	}
	.marketplace-cont .marketplace-main-cont .modal-dialog .modal-content .user-nft .nft-details button{
		font-size: xx-large;
	}
}