.navbar{
    /* border: 1px solid red; */
    height: min-content;
    display: grid;
    grid-template-columns: 1fr 50rem 10fr 1fr 1fr 1fr 15rem;
    grid-column: 2/3;
    width: 100%;
    margin-top: 15px;
}

.navbar > input[type=radio]{
    grid-column: 1/2;
    display: none;
}

.navbar > div{
    grid-column: 3/4;
    /* border: 1px solid red; */
}

.navbar > div::before{
    content: "";
    background: url("../../images/search.png");
    background-repeat: no-repeat;
    background-size: contain;
    font-size: large;
    position: absolute;
    padding: 18px;
    margin: 5px 2px;
}

.navbar > div input[type="search"]{
    min-width: 200px;
    height: 20px;
    padding: 15px 15px 15px 40px;
    font-size: large;
    border-radius: 10px;
    background-color: #f6f6f6;
    border: none;
}

.navbar span{
    font-size: 2rem;
    text-align: center;
    align-items: center;
    margin: 0 0.5rem;
}

.navbar span:nth-child(2){
    grid-column: 4/5;
}
.navbar span:nth-child(4){
    grid-column: 6/7;
}

.navbar span .btn-group > button{
   /* border: 1px solid red; */
   width: 15rem;
   font-size: large;
   border-radius: 20px;
   display: flex;
   justify-content: space-evenly;
   align-items: center;
}


@media (width < 768px){
    .navbar{
        position: sticky;
        top: 0;
        z-index: 5;
        background-color: #FFF;
        min-width: 700px;
        grid-column: 1/3;
        grid-template-columns:  1fr 1rem 5fr 1fr 1fr 5fr 20rem;
    }

    .navbar > input[type=radio]{
        display: block;
        grid-column: 1/2;
        appearance: none;
        width: 5rem;
        height: 5rem;
        background: linear-gradient(0deg, var(--light-green), var(--dark-green));;
        border-radius: 10px;
    }
    .navbar > input[type=radio]::after{
        content: '';
        position: absolute;
        /* border: 1px solid red; */
        padding: 2.4rem;
        background: url('../../images/Menu\ Icon/Menu-01.png');
        background-size: contain;
        background-position: center;
        cursor: pointer;
        border-radius: 10px;
    }
    .navbar > div::before{
        margin: 15px 2px;
    }
    .navbar > div input[type="search"]{
        width: 300px;
        height: 50px;
    }
    .navbar span{
        font-size: 5rem;
    }
    .navbar span .btn-group > button{
        margin-top:-15px;
        width: 20rem;
        font-size: 4rem;
    }
    
}

@media (width <= 479px){
    .navbar{
        min-width: 868px;
        grid-template-columns:  1fr 1rem 5fr 1fr 1fr 5fr 20rem;
    }
    .navbar > input[type=radio]{
        width: 8rem;
        height: 8rem;
    }
    .navbar > input[type=radio]::after{
        padding: 4rem;
    }
    .navbar > div::before{
        margin: 25px 2px;
        padding: 3rem;
    }
    .navbar > div input[type="search"]{
        width: 400px;
        height: 80px;
        font-size: xx-large;
        padding-left: 70px;
    }
    .navbar span{
        font-size: 5rem;
    }
    .navbar span .btn-group > button{
        margin-bottom: 2px;
        font-size: 4rem;
    }
}