:root {
    --light-green: #64bf3a;
    --dark-green: #1a9349;
}

.sidebar {
    z-index: 10;
    background: linear-gradient(180deg, var(--light-green), var(--dark-green));
    display: grid;
    width: min-content;
    height: min-content;
    margin: 10px 5px;
    padding: 20px 5px;
    border-radius: 14px;
    grid-template-rows: 1fr 10fr 1fr;
    justify-content: center;
    grid-column: 1/2;
    grid-row: 1/3;
}

.sidebar div:nth-child(1) {
    display: grid;
}

.sidebar div:nth-child(2) {
    display: grid;
    padding: 100px 0;
}

.sidebar div:nth-child(1) input[type="radio"] {
    appearance: none;
    margin: 0;
    cursor: pointer;
    background: url('../../images/speedometer.png');
    background-repeat: no-repeat;
    background-size: contain;
    width: 45px;
    height: 45px;
}

.sidebar div:nth-child(2) .link input[type="radio"] {
    appearance: none;
    border: 1px solid white;
    border-radius: 10px;
    background: linear-gradient(0deg, var(--light-green), var(--dark-green));
    margin: 10px 0;
    cursor: pointer;
    width: 45px;
    height: 50px;
}

.sidebar div:nth-child(2) .link:nth-child(1) input::after {
    content: " ";
    position: absolute;
    padding: 26px 21px;
    background: url('../../images/9.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
}

.sidebar div:nth-child(2) .link:nth-child(2) input::after {
    content: " ";
    position: absolute;
    padding: 26px 21px;
    background: url('../../images/10.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
}

.sidebar div:nth-child(2) .link:nth-child(3) input::after {
    content: " ";
    position: absolute;
    padding: 26px 21px;
    background: url('../../images/11.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
}

.sidebar div:nth-child(2) .link:nth-child(4) input::after {
    content: " ";
    position: absolute;
    padding: 26px 21px;
    background: url('../../images/12.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
}

.sidebar div:nth-child(2) .link:nth-child(1) input[type="radio"]:checked,
.sidebar div:nth-child(2) .link:nth-child(2) input[type="radio"]:checked,
.sidebar div:nth-child(2) .link:nth-child(3) input[type="radio"]:checked,
.sidebar div:nth-child(2) .link:nth-child(4) input[type="radio"]:checked {
    background: #fff;
    width: 120%;
    border-radius: 50px 0px 0px 50px;
    height: 50px;
}

.sidebar div:nth-child(2) .link:nth-child(1) input[type="radio"]:checked::after {
    content: "Market place";
    text-align: right;
    font-size: 1.5rem;
    color: #3fb89a;
    position: absolute;
    padding: 15px 6px 15px 50px;
    background: url('../../images/5.png');
    background-color: #fff;
    border-radius: 50px 0px 0px 50px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
    background-position-x: left;
}

.sidebar div:nth-child(2) .link:nth-child(2) input[type="radio"]:checked::after {
    content: "My Farm";
    text-align: right;
    font-size: 1.5rem;
    color: #3fb89a;
    position: absolute;
    padding: 15px 6px 15px 50px;
    background: url('../../images/6.png');
    background-color: #fff;
    border-radius: 50px 0px 0px 50px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
    background-position-x: left;
}

.sidebar div:nth-child(2) .link:nth-child(3) input[type="radio"]:checked::after {
    content: "Live Farm";
    text-align: right;
    font-size: 1.5rem;
    color: #3fb89a;
    position: absolute;
    padding: 15px 6px 15px 50px;
    background: url('../../images/7.png');
    background-color: #fff;
    border-radius: 50px 0px 0px 50px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
    background-position-x: left;
}

.sidebar div:nth-child(2) .link:nth-child(4) input[type="radio"]:checked::after {
    content: "Games";
    text-align: right;
    font-size: 1.5rem;
    color: #3fb89a;
    position: absolute;
    padding: 15px 6px 15px 50px;
    background: url('../../images/8.png');
    background-color: #fff;
    border-radius: 50px 0px 0px 50px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: center;
    background-position-x: left;
}


.sidebar div:nth-child(3) img {
    margin-top: 10px;
    margin-left: 5px;
    cursor: pointer;
}


@media (width<768px) {
    .sidebar {
        display: none;
    }

    .App:has(.navbar>input[type="radio"]:checked).sidebar div:nth-child(2) {
        align-items: stretch;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar {
        display: grid;
        align-items: flex-start;
        grid-template-rows: 1fr 5fr 1fr;
        position: fixed;
        margin: 0;
        border-radius: 0;
        width: 100%;
        height: 100%;
        animation-name: filler;
        animation-duration: 1s;
        animation-timing-function: ease;
        animation-iteration-count: 1;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(1) input[type="radio"]::after {
        content: 'Dashboard';
        position: absolute;
        font-size: xx-large;
        padding-top: 5rem;
        margin-left: -5rem;
        font-weight: bolder;
        color: #fff;
    }

    .sidebar div:nth-child(2) {
        height: 200px;
    }

}


@media (width<479px) {

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(1) input[type="radio"] {
        width: 120px;
        height: 120px;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(1) input[type="radio"]::after {
        font-size: 7rem;
        padding-top: 10rem;
        margin-left: -10rem;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link input[type="radio"] {
        appearance: none;
        width: 120px;
        height: 120px;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(1) input::after,
    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(2) input::after,
    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(3) input::after,
    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(4) input::after {
        padding: 60px;
    }

    .sidebar div:nth-child(3) img {
        width: 120px;
        height: 120px;
    }
}

@media (width<299px) {
    .App:has(.navbar>input[type="radio"]:checked) .sidebar {
        grid-template-rows: 1fr 2fr 1fr;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(1) input[type="radio"] {
        width: 160px;
        height: 160px;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(1) input[type="radio"]::after {
        font-size: 10rem;
        padding-top: 15rem;
        margin-left: -15rem;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link input[type="radio"] {
        width: 160px;
        height: 160px;
    }

    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(1) input::after,
    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(2) input::after,
    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(3) input::after,
    .App:has(.navbar>input[type="radio"]:checked) .sidebar div:nth-child(2) .link:nth-child(4) input::after {
        padding: 80px;
    }

    .sidebar div:nth-child(3) img {
        width: 160px;
        height: 160px;
    }
}

@keyframes filler {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}