*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
}

html{
  scroll-behavior: smooth;
  font-size: 62.5%;
}
body{
  overflow: hidden;
}

.App{
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 20fr;
  flex-wrap: wrap;
  height: 100%;
}

.App:has(.sidebar div:nth-child(1) input[type="radio"]:checked){
  min-width: 100%;
  min-height: 100%;
  background: url("https://uploads-ssl.webflow.com/60ccb5b3bd077c65f47edce9/60ccb5b3bd077c67827edd6b_iStock-577348440-2.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.App:has(.sidebar div:nth-child(1) input[type="radio"]:checked) .navbar > div input[type="search"]{
  background-color: rgba(255, 255, 255, 0.372);
}

.App:has(.sidebar div:nth-child(1) input[type="radio"]:checked) .navbar span .btn-group > button{
  border-color: rgba(255, 255, 255, 0.372);
  background-color: rgba(255, 255, 255, 0.372);
}

.content{
  grid-column: 2/3;
  /* border: 1px solid red; */
  width: 100%;
  height: 90vh;
  overflow-y: auto;
}

@media (width<768px){
  body{
    overflow-y: auto;
    overflow-x: hidden;
  }
  .content{
    height: 100%;
    grid-column: 1/4;
  }
 
}