:root {
  --primary: rgba(240, 248, 255, 0.615);
  --light-green: #64bf3a;
  --dark-green: #1a9349;
}

.dashboard {
  background-color: rgba(255, 255, 255, 0.229);
  display: grid;
  grid-template-columns: 5rem 3fr 1fr 2rem;
  gap: 10px;
  padding: 20px 0;
  border-radius: 14px;
  margin-top: 20px;
}

.dashboard img {
  border-radius: 50%;
}

.dashboard .dashboard-div {
  grid-column: 2/3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  /* background-color: aquamarine; */
  gap: 20px;
  padding: 20px;
  font-size: larger;
}

.dashboard .dashboard-div .user-name {
  grid-column: 1/3;
}
.dashboard .dashboard-div .my-balance {
  background-color: var(--primary);
  grid-column: 1/3;
  padding: 10px;
  border-radius: 14px;
}

.dashboard .dashboard-div .my-balance .my-balance-div {
  margin-top: 25px;
  display: flex;
}
.dashboard .dashboard-div .my-balance .my-balance-div img {
  margin-right: 10px;
}

.dashboard .dashboard-div .my-balance .my-balance-div div {
  display: flex;
  width: 90%;
  justify-content: space-between;
}

.dashboard .dashboard-div .token-balance,
.dashboard .dashboard-div .nftress {
  background-color: var(--primary);
  padding: 15px 15px 0;
  border-radius: 14px;
}
.dashboard .dashboard-div .token-balance > div,
.dashboard .dashboard-div .nftress div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 2rem;
  margin-bottom: 25px;
}

.dashboard .dashboard-div .nftress div span img {
  margin-right: 10px;
}

.dashboard .dashboard-div .nftress button {
  border: none;
  padding: 4px 18px;
  border-radius: 50px;
  background-color: var(--dark-green);
  font-size: large;
  color: #fff;
  transition: all 1s;
  transition-timing-function: cubic-bezier(0.1, 0.3, 0.5, 0.1);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.dashboard .dashboard-div .nftress button:hover {
  background-color: var(--light-green);
}
.dashboard .dashboard-div .nftress button:active {
  background-color: var(--light-green);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.dashboard .dashboard-div .nftress > button {
  margin-top: 25px;
}

.dashboard .dashboard-div .token-balance .token-balance-div {
  /* background-color: #64bf3a; */
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 0 70px;
  gap: 30px;
}

.dashboard .dashboard-div .token-balance .token-balance-div span {
  background-color: var(--primary);
  padding: 14px 14px 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 14px;
}
.dashboard .dashboard-div .token-balance .token-balance-div span h4 {
  margin-top: 3px;
}

.dashboard .dashboard-live {
  background-color: var(--primary);
  padding: 10px;
  border-radius: 14px;
}

.dashboard .dashboard-live > div span {
  font-size: x-large;
}

.dashboard .dashboard-live > div img {
  margin-right: 10px;
  margin-top: -10px;
}

.dashboard .dashboard-live .live-statistics {
  margin-top: 20px;
}

.dashboard .dashboard-live .live-statistics p {
  font-size: 1.3rem;
  display: flex;
  justify-content: space-between;
}

.dashboard .dashboard-live .top-holders h2 {
  margin: 20px 0;
}

.dashboard .dashboard-live .top-holders ul {
  padding: 0;
  /* border: 1px solid red; */
  height: 200px;
  overflow-y: auto;
}

.dashboard .dashboard-live .top-holders ul li {
  list-style: decimal !important;
  display: flex;
  background-color: var(--primary);
  padding: 14px 14px 5px;
  margin: 10px 0;
  border-radius: 24px;
}

.dashboard .dashboard-live .top-holders ul li img{
    margin-left: 5px;
}

.dashboard .dashboard-live .top-holders ul li p {
  font-size: larger;
  width: 70%;
}

@media (width<=990px) {
    .App:has(.sidebar div:nth-child(1) input[type="radio"]:checked) {
    background: none;
  }
  .App:has(.sidebar div:nth-child(1) input[type="radio"]:checked) .navbar{
    background-color: #fff;
  }
  .dashboard {
    background-color: var(--light-green);
  }
  .dashboard .dashboard-div {
    grid-column: 2/4;
  }
  .dashboard .dashboard-live {
    grid-column: 2/4;
  }
  .dashboard .dashboard-div .token-balance,
  .dashboard .dashboard-div .nftress {
    grid-column: 1/3;
    padding-bottom: 10px;
  }
  .dashboard .dashboard-live{
    margin: 0 20px;
  }
  .dashboard .dashboard-live .top-holders ul {
    height: max-content;;
  }
}

@media (width<=479px){
    .dashboard ,
    .dashboard .dashboard-live .live-statistics p {
        font-size: x-large;
    }
    .dashboard .dashboard-live .top-holders ul li {
        align-items: center;
    }
}
