

.myfarm .marketplace-head {
  margin-top: 10px;
  max-width: 100%;
  /* border: 1px solid red; */
  display: grid;
  place-items: center;
  grid-template-areas:
    "a c"
    "b c";
  grid-template-columns: 1fr 200px;
  grid-template-rows: 45px 1fr;
  position: sticky;
  top: 0;
  z-index: 2;
  background-color: #fff;
}

.myfarm .marketplace-head h2 {
  /* border: 1px solid blue; */
  padding: 0;
  grid-area: a;
  color: grey;
  text-transform: uppercase;
  font-size: 3rem;
  text-align: center;
}

.myfarm .marketplace-head .marketplace-buttons-cont {
  /* border: 1px solid grey; */
  grid-area: b;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-inline: 10%;
  margin: 10px 0;
}

.myfarm .marketplace-head .marketplace-buttons-cont button {
  margin: 5px;
  border-radius: 15px;
  border: none;
  outline: none;
  background-color: var(--dark-green);
  width: 120px;
  height: 40px;
  font-size: 1.5rem;
  font-weight: bolder;
  text-transform: uppercase;
  color: white;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 1px 7px;
  transition: all 0.4s ease-out ;
  transform-origin: top left ;
}

.myfarm .marketplace-head .marketplace-buttons-cont button:hover{
    background-color:  var(--light-green);
}
.myfarm .marketplace-head .marketplace-buttons-cont button:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.myfarm .marketplace-head .marketplace-head-right {
  grid-area: c;
  background-color: rgb(15, 117, 10);
  border-radius: 20px;
  display: grid;
  padding: 4px 0;
  grid-template-areas:
    "x y"
    "x z";
  /* border: 1px solid red; */
}

.myfarm .marketplace-head .marketplace-head-right p {
  text-align: center;
  position: relative;
  left: -15px;
  top: 3px;
  color: white;
  font-size: 18px;
  /* border: 1px solid red; */
  margin: 0;
  z-index: 0;
}

.myfarm .marketplace-head .marketplace-head-right p:nth-child(3) {
  font-weight: bolder;
  /* border: 1px solid red; */
  padding-bottom: 4px;
}

.myfarm .marketplace-head .marketplace-head-right .hijklmno {
  grid-area: x;
  border: 2px solid white;
  background: url(../../images/logoo.png);
  background-repeat: no-repeat;
  background-size: 100%;
  background-color: var(--light-green);
  border-radius: 10px;
  position: relative;
  left: -20px;
  top: 2px;
  background-position: center;
  width: 50px;
  height: 50px;
}

.myfarm .myfarm-data {
  display: grid;
  grid-template-columns: repeat(3, 3fr);
  margin-left: 60px;
  flex-wrap: wrap;
}

.myfarm .myfarm-data div:hover {
  filter: none !important;
}

@media (width <= 1082px){
  .myfarm .myfarm-data {
    grid-template-columns:  repeat(2,1fr);
    margin-left: 0px;
  }

  .myfarm .myfarm-data div{
    min-height: 600px !important;
  }
  
}
@media (width <= 479px){
  .myfarm .myfarm-data {
    grid-template-columns:  repeat(1,1fr);
    margin-left: 0px;
  }

  .myfarm .myfarm-data div{
    min-height: 1000px !important;
  }
}

